	import headers from "../../components/header_nav.vue";
	import btncoms from "../../components/btncom.vue";
	import service from "../../service.js";
export default {
	components:{
		headers , btncoms
	},
	data() {
		return {
			Htitle:"文章详情",
			Hlicon:"../../static/icon/left_whitch.png",
			btntitle:"发布评价",
			bitcolor:"",
			clause:true,
			id:'',
			type:'',
			textconts:'',
			thumb:'',
			avatar:'',
			createtime:'',
			content:'',
			nickname:'',
			showmask:false,
			importcont:'',
			url_left:"/pages/index/everyoneg"
		};
	},
	methods:{
		jump(){
				uni.navigateBack({
					delta:1
				})
		},
		danswind(){
			this.showmask = !this.showmask;
		},
		mask(){
			this.showmask = !this.showmask;
		},
		lookMore(){
			uni.navigateTo({
				url:'/pages/mycar/allEvaluation?id='+this.id                 //+'&type='+this.type 
			})
		},
		commit(){
			service.auth(this , service.api.index.commentSubmit , 'POST' , {
				article_id:this.id,
				content:this.importcont
			} , function(self , res){
				
				console.log(res);
				uni.showToast({
					title:res.info,
					icon:'none'
				});
				setTimeout(()=>{
					self.showmask = false;
					// this.totle()
					},500)
					// uni.redirectTo({
					// 	url:'/pages/mycar/car-details'
					// })
			})
		}
	},
	onShow(){
		
	},
	  onLoad(op){
				this.id = op.id;
				this.type = op.type;
		
				console.log(this.id,this.type);
			
					service.auth(this , service.api.index.cararticleDetail , 'GET' , {
						id:op.id,
						limit:2,
						// type:op.type
					} , function(self , res){
						console.log(res);
						self.textconts = res.data.article.content;
						self.thumb = res.data.article.thumb;
						self.avatar = res.data.comment.data[0].avatar;
						self.createtime = res.data.comment.data[0].createtime;
						self.content = res.data.comment.data[0].content;
						self.nickname = res.data.comment.data[0].nickname;
					})
				
				
	      }
};
